.App {
  text-align: center;
}

.App main {
  min-height: calc(100vh - 40px);
  display: flex;
  justify-content: center;
  position: relative;
}

.App-sider {
  background-color: white;
  width: 350px;
}

.App-header {
  background-color: #282c34;
  min-height: 40px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 0 20px;
}

.App-header h1 {
  color:#ffec3d;
  font-size: 26px;
  margin: 0;
}

.App-link {
  color: #61dafb;
}
